






































import { Component, Vue, Provide, Emit, Watch } from 'vue-property-decorator';
import { Layout, Menu, Breadcrumb } from 'ant-design-vue';
import { Route } from 'vue-router';
import request from '@/middleware/request';
import Admin from '@/Layout/Admin.vue';
const { Header, Content, Footer } = Layout;
const { Item } = Breadcrumb;
const MenuItem = Menu.Item;
@Component({
  components: {
    'a-layout': Layout,
    'a-header':Header,
    'a-content': Content,
    'a-footer': Footer,
    'a-menu': Menu,
    'a-breadcrumb': Breadcrumb,
    'a-item': Item,
    'a-menuitem':MenuItem,
    'a-admin': Admin,
  },
})
export default class BasicLayout extends Vue {
  @Provide() public isAuth = false;
  @Provide() public meunKey: any = 0;

  public async beforeRouteEnter(to: any, from: any, next: any) {
    const toPath = to.path,
          fromPath = from.path;
    if (toPath.indexOf('/user') < 0) {
      const { code } = await request({
        api: 'http://localhost:8080/api/user/getUser',
      });
      if (code === 401) {
        next('/user/login');
      }
    }
    next((vm: any) => {
      vm.isAuth = true;
    });
  }

  public beforeRouteUpdate(to: any, from: any, next: any) {
    const { isAuth } = this;
    if (!isAuth) {
      next('/user/login');
    }
    next();
  }

  @Emit('loginout')
  public async loginout() {
    const res = await request({
        api: '/api/user/loginout',
        method: 'POST',
    });
    if (res.code === 1) {
      this.isAuth = false;
      this.$router.push('/user/login');
    }
  }

  /**
   * 路由变化监听，根据路由变化做相应操作
   */
  @Watch('$route')
  public watchRoute(val: any, old: any) {
    console.log(val);
    console.log('');
    console.log(old);
  }
}
